@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Quicksand:400,500,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Quicksand', sans-serif;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 0;
  overflow: hidden;
  text-align: center;
  width: 100vw;
}

.header {
  align-items: center;
  /*background: #484158;*/
  background: white;
  /*border-bottom: 1px solid #E1ECF5;*/
  display: flex;
  font-weight: bold;
  height: 60px;
  text-transform: uppercase;
  box-shadow: 0 0 5px 0 #e8e8e8;
}

.header .title {
  /*color: white;*/
  /*color: #9CB0C0;*/
  color: #394752;
  margin-left: 5em;
  font-weight: bold;
}

.content {
  margin: 2em 5em;
  text-align: left;
}

p {
  color: #9CB0C0;
  font-size: 1.1em;
  font-weight: 600;
  margin: 0 0 0 20px;
}

.button-group {
  display: flex;
  align-items: center;
}

.inputFile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.inputFile+label {
  font-size: .9em;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  padding: 0.625rem 1.25rem;
  color: #777;
  border: solid 1px #e8e8e8;
  border-radius: 18px;
  margin: 10px 0 20px 20px;
}

.inputFile:focus+label,
.inputFile+label:hover {
  border: solid 1px #9CB0C0;
  /*color: #9CB0C0;*/
}

.inputFile+label svg {
  width: 1em;
  height: 1em;
  vertical-align: middle;
  fill: currentColor;
  margin-top: -0.25em;
  margin-right: 0.25em;
}

.fa-upload {
  margin-right: 10px;
}

.filepond--wrapper .filepond--root {
  margin-top: 20px;
  /*width: 26vw;*/
}

.filepond--panel-top,
.filepond--panel-center,
.filepond--panel-bottom {
  background: transparent !important;
}

.filepond--drip,
.dropzone {
  background: white !important;
  opacity: 1 !important;
  /*box-shadow: 0 0 5px 0 #e8e8e8 !important;*/
  border: dotted 2px #E1ECF5 !important;
  margin: 20px !important;
  /*border-radius: 30px !important;*/
  outline: none;
  height: 50px;
  cursor: pointer;
  color: #777 !important;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filepond--file {
  color: #777 !important;
}

.dropzone:hover {
  border: dotted 2px #9CB0C0 !important;
  color: #9CB0C0 !important;
}

.dropzone--isDone {
  /*justify-content: space-between;*/
}

.file-name {
  margin-left: 20px;
}

.file-size {
  font-size: .8em;
  text-align: center;
}

p {
  color: #7888BF;
}

.upload-message {
  font-weight: 400;
}

span {
  color: #777;
}

#calendar {
  /*font-family: 'Montserrat', sans-serif;*/
  margin: 20px;
}

.month {
  /*margin-right: 8px;*/
}

.month-name,
#calendar g {
  fill: #777;
  font-weight: 500;
}

.tooltip {
  background: #394752;
  color: white;
  /*background: white;*/
  border-radius: 10px;
  /*border: solid 1px #e8e8e8;*/
  display: block;
  font-size: 85%;
  font-weight: bold;
  line-height: 30px;
  /*opacity: .8;*/
  overflow: hidden;
  padding: 10px 20px;
  position: fixed;
}

#calendar svg {
  height: auto;
  width: auto;
}

#calendar g {
  font-size: 74%;
  line-height: 1.5em;
  position: absolute;
  top: 331px;
  margin-left: 20px;
}

#calendar .day {
  cursor: pointer;
}

.dayLabels {
  color: #777;
  display: block;
}

.months {
  margin-left: 60px;
}

.tooltip .text {
  font-weight: 400 !important;
}

#calendar {
  width: calc(100vw - 200px);
  display: inline-block;
  /*border-radius: 30px;*/
  box-shadow: 0 0 5px 0 #e8e8e8;
  background: white;
}

.overlay {
  text-align: center;
  position: relative;
  z-index: 4;
  font-size: 40px;
  top: 60px;
  color: purple;
  width: calc(100vw - 40px);
}

.hoveredMonths {
  -webkit-filter: blur(3px);
          filter: blur(3px);
}

.yearLabel {
  font-size: 1.2em;
  font-weight: 500;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  border-bottom: solid 1px #f1f1f1;
  /*border-radius: 40px;*/
  margin-bottom: 24px;
  color: #777;
}

.fa-chevron-left {
  margin-left: 1.2em;
}

.fa-chevron-right {
  margin-right: 1.2em;
}

.hidden {
  display: none;
}

.yearLabel i {
  cursor: pointer;
}

.year {
  margin: 26px 0;
}

.current-day {
  stroke: #777;
  stroke-width: 2px;
}

.yearLabel .disabled {
  color: #f1f1f1;
  cursor: not-allowed;
}

